import http from "@/utils/request";

export const getScreenshots = (data) => {
    return http.post("/screenshots/get", data)
}

export const addScreenshots = (data) => {
    return http.post("/screenshots/insert", data)
}

export const updateScreenshots = (data) => {
    return http.post("/screenshots/update", data)
}

export const deleteScreenshots = (data) => {
    return http.post("/screenshots/delete", data)
}

export const uploadGoogle = (data) => {
    let config = {
        headers: {"Content-Type": "multipart/form-data"}
    }
    return http.postForm("/screenshots/upload", data, config)
}